import React, { Component } from 'react';
import { LineChart, XAxis, YAxis, CartesianGrid, Line } from 'recharts';

const dataNReparaveis = [
			{
				quantidade: 0,
				valor: 38.0
			},
			{
				quantidade: 1,
				valor: 74.8
			},
			{
				quantidade: 2,
				valor: 92.6
			},
			{
				quantidade: 3,
				valor: 98.3
			},
			{
				quantidade: 4,
				valor: 99.7
			},
			{
				quantidade: 5,
				valor: 99.9
			},
			{
				quantidade: 6,
				valor: 100
			}
		];

const dataReparaveis = [
			{
				quantidade: 0,
				valor: 77.01
			},
			{
				quantidade: 1,
				valor: 97.13
			},
			{
				quantidade: 2,
				valor: 99.76
			},
			{
				quantidade: 3,
				valor: 99.98
			},
			{
				quantidade: 4,
				valor: 100
			},
			{
				quantidade: 5,
				valor: 100
			},
			{
				quantidade: 6,
				valor: 100
			}
		];

class Chart extends Component {

	constructor(props){
		super();
		this.state = {
			tab: "n-reparaveis"
		}
	}

	render () {

		let data = [];
		if( this.state.tab === 'reparaveis' ){
			data = dataReparaveis;
		} else if( this.state.tab === 'n-reparaveis' ){
			data = dataNReparaveis;
		}

		let quantidade = 0;

		let nivel = this.props.nivelConfiab;

		for(var i = 0; i< data.length; i++){

			if( nivel <= data[i].valor ){
				quantidade = data[i].quantidade;
				break;
			}

		}

		return (
			<div className="Chart">
				<div className="flex flex-center">
					<span 
						onClick={() => this.setState({ tab: "n-reparaveis" })} 
						className={this.state.tab === "n-reparaveis" ? "active" : ""} >
						Não Reparavéis
					</span>
					<br />
					<span 
						className={this.state.tab === "reparaveis" ? "active" : ""} 
						onClick={() => this.setState({ tab: "reparaveis" })} >
						Reparavéis
					</span>
					<br />
					<br />
				</div>
				<div>
					<LineChart width={600} height={300} data={data}>
					    <XAxis dataKey="quantidade" label="quantidade" />
					    <YAxis label="%"/>
					    <CartesianGrid stroke="#eee" strokeDasharray="5 5"/>
					    <Line type="monotone" dataKey="valor" stroke="#8884d8" />
					</LineChart>
					<br /><br />
					<h1 className="text-center">Quantidade: {quantidade}</h1>
				</div>
			</div>
		);

	}

}

export default Chart;


