import React, {Component} from 'react';

class Header extends Component {

	render () {

		return (
			<div className="Header">
				<h1 style={{
					backgroundColor: (this.props.nivelConfiab >=90 && this.props.nivelConfiab <=92 ) ? "#308419" : "#ce3010",
					color: "#EFEFEF",
					fontSize: 35,
					margin: 0,
					padding: 20
				}} >
					{(this.props.nivelConfiab >=90 && this.props.nivelConfiab <=92 )?"":"NÃO "}IDEAL
				</h1>
			</div>
		);

	}

}

export default Header;


