import React, { Component } from 'react';

class Slider extends Component {

	render () {

		return (
			<div className="Slider flex vertical flex-center">
					<span className="flex-1">
						<h2>Nível de Confiabilidade:&nbsp;{ this.props.nivelConfiab || 0 }%</h2>
					</span>
					<br />
					<input 
						className="flex-1" 
						min={0}
						max={100} 
						type="range"
						onChange={this.props.onChange}
						value={this.props.nivelConfiab} />
			</div>
		);

	}

}

export default Slider;


